.detailProduct {
  display: flex;
  gap: 20px;
  margin: 20px auto;
  max-width: 1200px;
  background-color: rgba(219, 219, 219, 0.186);
}

.detailProduct .miniImg {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 300px;
}

.detailProduct .miniImg img {
  max-width: 120px;
  width: 100%;
  /* height: auto; */
}

.detailProduct .miniImg img:hover {
  cursor: pointer;
  transform: scale(3);
  transition: 0.3s linear;
}

.detailProduct .imgMain {
  max-width: 480px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.detailProduct .imgMain img {
  max-width: 480px;
  width: 100%;
  /* height: auto; */
}

.detailProduct .detailDescription {
  max-width: 600px;
  display: flex;
  flex-direction: column;
  padding: 0 30px;
}

.detailProduct .detailDescription h1 {
  margin: 20px 0;
}

.detailProduct .detailDescription h3 {
  margin-bottom: 20px;
}

.detailProduct .detailDescription p {
  font-size: 16px;
  margin-bottom: 20px;
  line-height: 1.4;
  color: #333;
}

.middle {
  max-width: 1200px;
  margin: 0 auto;
}

.middle span {
  color: #fff;
  background-color: #333;
  padding: 5px 10px;
  border-radius: 3px;
  font-size: 22px;
}

.middle h3,
.bottom h3 {
  margin: 20px 0;
}

.middle .description {
  max-width: 600px;
  margin: 10px 0;
  font-size: 16px;
}

.bottom {
  max-width: 1200px;
  margin: 0 auto;
  margin-bottom: 20px;
}

.bottom .products {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(3, 1fr);
}

.detailProduct .formButton {
  display: flex;
  gap: 20px;
}
.detailProduct .formButton form {
  border: 1px solid rgba(0, 0, 0, 0.2);
  width: 200px;
  height: 40px;
  font-size: 18px;
  padding: 0 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.detailProduct .formButton form input {
  border: none;
  outline: none;
  font-size: 18px;
}

.detailProduct .formButton button {
  background-color: #333;
  color: #fff;
  font-size: 18px;
  border-radius: 5px;
  height: 40px;
  width: 200px;
  padding: 5px 10px;
}

.detailProduct .formButton button:hover {
  cursor: pointer;
}

.backToShopBtn button {
  position: fixed;
  z-index: 20;
  bottom: 50px;
  right: 200px;
  width: 200px;
  height: 50px;
  background-color: #333;
  color: #fff;
  font-size: 18px;
  padding: 5px 10px;
  border-radius: 10px;
  box-shadow: 4px 4px rgba(0, 0, 0, 0.218);
}

.backToShopBtn button:before {
  content: " \2190";
}

.backToShopBtn button:hover {
  cursor: pointer;
}

@media (max-width: 768px) {
  .detailProduct {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .detailProduct .miniImg {
    display: flex;
    flex-direction: row;
    gap: 20px;
    max-width: none;
  }
  .detailProduct .miniImg img:hover {
    transform: scale(1);
  }

  .middle {
    margin: 40px;
  }

  .bottom {
    margin: 40px;
  }

  .backToShopBtn button {
    position: fixed;
    right: 10px;
    bottom: 10px;
  }
}

@media (max-width: 500px) {
  .detailProduct .miniImg {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    max-width: none;
  }
}
